.rebote-animation {
    animation: reboteIn 1s ease;
}

@keyframes reboteIn {

    0% {
        opacity: 0;
        transform: scale(.1);
    }

    50% {
        opacity: 1;
        transform: scale(1);
    }

    70% {
        opacity: 1;
        transform: scale(0.9);
    }

}